.our_products {
  max-width: 1320px;
  margin: 0 auto;

  padding-top: 80px;

  h1{
    padding-bottom: 28px;
    font-family: 'Orenburg';
    font-size: 32px;
    font-weight: 400;
    line-height: 36px;
    display: flex;
    align-items: center;
    letter-spacing: -0.5;
    color: #151716;
  }
}

.products_row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 100px;
}

.products_row_item {
  background: url("../img/img.png") no-repeat;
  background-size: cover;
  width: 292px;
  height: 350px;

  display: flex;
  flex-direction: column;

  align-items: start;
  justify-content: end;


  h2 {
    font-family: 'Orenburg';
    font-size: 20px;
    line-height: 22px;
    color: #FFFFFF;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 35px;
  }

  hr{
    margin: 0 auto;
    color: #FFFFFF;
    background-color: #FFFFFF;
    border: 0;
    height: 1px;
    width: 220px;

  }
}