* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,h2,h3,h4,h5,h6,p {
  font-size: inherit;
}

body {
}

input, button {
  font-family: inherit;
  font-size:inherit;
}

input:focus, button:focus{
  outline: none;
}


ul,li{
  list-style: none;
}

a {
  text-decoration: none;
  color:inherit;
}