.item-right {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 10px;
}
.item-right .phone {
  padding-left: 9px;
  padding-right: 32px;
}
.item-right .icon {
  justify-content: center;
  background: #222225;
  cursor: pointer;
  height: 40px;
  width: 40px;
  text-align: center;
  border: 1px solid #494949;
  border-radius: 2px;
}
.item-right .cart {
  justify-content: center;
  gap: 10px;
  background: #222225;
  cursor: pointer;
  display: flex;
  align-items: center;
  width: 118px;
  height: 40px;
  border: 1px solid #494949;
  border-radius: 2px;
}
.item-right p {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  display: flex;
  align-items: center;
  text-align: right;
  color: #999999;
}
.item-right button {
  color: white;
}

.header {
  background: #222225;
  display: block;
  width: 100%;
  position: relative;
  z-index: 99;
  padding: 15px;
}
.header .menu ul li {
  display: inline-block;
  margin-left: 25px;
}
.header .menu ul li a.menu_link {
  color: #BEBEBE;
  font-size: 18px;
  position: relative;
}
.header .menu ul li .sub-menu {
  top: 90px;
  position: absolute;
  z-index: 500;
  background-color: white;
  padding: 30px;
  box-shadow: -2px 2px 70px -25px rgba(0, 0, 0, 0.3);
}
.header .menu ul li .sub-menu.mega-menu {
  left: 50%;
  transform: translateX(-50%);
}
.header .menu ul li .sub-menu.mega-menu-column-4 {
  max-width: 1320px;
  width: 100%;
}
.header .menu ul li .sub-menu ul li {
  line-height: 1;
}
.header .menu-item-has-children div {
  visibility: hidden;
  transition: all 0.3s ease;
}
.header .menu-item-has-children:hover div {
  visibility: visible;
}

.header_container {
  padding-top: 10px;
  padding-bottom: 10px;
  max-width: 1320px;
  margin: 0 auto;
}

.header .menu > ul > li {
  display: inline-block;
}

.header .item-left {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex: 0 0 10%;
}

.header .item-center {
  flex: 0 0 55%;
}

.header .item-right {
  flex: 0 0 35%;
}

.header_row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.row_item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 56px;
  padding-bottom: 56px;
}
.row_item .item_item_1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 0 0 25%;
  align-items: center;
}
.row_item .item_item_2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 0 0 25%;
  align-items: center;
}
.row_item .item_item_3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 0 0 25%;
  align-items: center;
}
.row_item .item_item_4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 0 0 25%;
  align-items: center;
}
.row_item a {
  padding-top: 30px;
  font-family: "Orenburg";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  color: #151716;
}
.row_item a:hover {
  color: #EC7133;
}

