.footer {
  background: #202122;
  hr {
    height: 1px;
    border: 0;
    background: #2C2D2E;
  }
}

.footer_row {

  max-width: 1320px;
  margin: 0 auto;
  padding-top: 60px;
  display: flex;
  justify-content: space-between;

  font-family: 'PT Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #BEBEBE;
}

.footer_links {

}

.footer_item{
  div {
    padding-bottom: 20px;
    display: flex;
    gap:12px;
    align-items: center;
  }
}

.footer_bold_text {
  padding-bottom: 20px;
  font-family: 'Orenburg';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 17px;
  color: #FFFFFF;
}

.text_row {
  max-width: 1320px;
  margin: 0 auto;
  padding-bottom: 24px;
  display: flex;
  flex-wrap: wrap;
  gap:24px;
  justify-content: end;
  align-items: center;

  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #747474;

}

.text_border {
  padding: 10px;
  width: 164px;
  border: 1px solid #494949;
  border-radius: 1px;
}

.svg_bottom {
  padding: 42px;
  text-align: center;

  button {
    border: none;
    cursor: pointer;
    background: #202122;
  }
}

